/* =====================
   RESET
===================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* =====================
   BASE
===================== */
html {
  font-size: 20px;
}

body {
  min-height: 100vh;
  font-family: 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  background-color: #f6f7fb;
  color: #222;
}

/* =====================
   APP CONTAINER
===================== */
.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 500px;
  margin: auto;
  background-color: #fff;
  border-radius: 12px;
  border: 1px solid #ddd;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
  overflow: hidden;
}

/* =====================
   HEADER
===================== */
header {
  padding: 1rem 1.2rem;
  background: linear-gradient(135deg, #0000cc, #3b5bdb);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.3rem;
  font-weight: 600;
}

/* =====================
   MAIN
===================== */
main {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0.75rem;
}

/* =====================
   FOOTER
===================== */
footer {
  background-color: #0000cc;
  color: white;
  text-align: center;
  padding: 0.6rem;
  font-size: 0.85rem;
}

/* =====================
   LIST
===================== */
ul {
  list-style: none;
  padding: 0;
}

/* =====================
   LIST ITEM (BIG SIZE)
===================== */
.item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.1rem 1.25rem;     /* ðŸ”¥ BIGGER SIZE */
  margin-bottom: 0.75rem;
  background-color: #f1f3f6;
  border-radius: 12px;
  transition: background-color 0.2s ease;
}

.item:hover {
  background-color: #e4e9f5;
}

/* =====================
   CHECKBOX (BIG)
===================== */
.item input[type="checkbox"] {
  width: 28px;
  height: 28px;
  cursor: pointer;
  accent-color: #0000cc;
}

/* =====================
   TEXT (BIG)
===================== */
.item label {
  flex-grow: 1;
  font-size: 1.1rem;          /* ðŸ”¥ BIG TEXT */
  font-weight: 500;
  word-break: break-word;
}

/* =====================
   DELETE ICON (BIG)
===================== */
.item svg {
  width: 28px;
  height: 28px;
  color: steelblue;
  cursor: pointer;
  transition: color 0.2s ease, transform 0.2s ease;
}

.item svg:hover {
  color: red;
  transform: scale(1.15);
}

/* =====================
   ADD FORM
===================== */
.addForm {
  display: flex;
  gap: 0.5rem;
  padding: 0.75rem;
  border-bottom: 1px solid #ddd;
}

.addForm label {
  position: absolute;
  left: -9999px;
}

.addForm input {
  flex-grow: 1;
  height: 48px;
  font-size: 1rem;
  padding: 0.4rem 0.6rem;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.addForm input:focus {
  outline: none;
  border-color: #0000cc;
}

/* =====================
   SEARCH FORM
===================== */
.searchForm {
  display: flex;
  padding: 0.75rem;
  border-bottom: 1px solid #ddd;
}

.searchForm label {
  position: absolute;
  left: -9999px;
}

.searchForm input {
  flex-grow: 1;
  height: 48px;
  font-size: 1rem;
  padding: 0.4rem 0.6rem;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.searchForm input:focus {
  outline: none;
  border-color: #0000cc;
}

/* =====================
   BUTTON
===================== */
button {
  height: 48px;
  min-width: 48px;
  border-radius: 8px;
  border: none;
  background-color: #0000cc;
  color: white;
  font-size: 1.3rem;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.1s ease;
}

button:hover {
  background-color: limegreen;
}

button:active {
  transform: scale(0.95);
}
